import { ref, onMounted, computed, watch } from "vue";
import { useStore } from "vuex";
import { ElMessage } from "element-plus";
import 'element-plus/es/components/message/style/index'

import { io } from "socket.io-client";
import DashboardTableForm from "./DashboardTableForm.vue";
import axios from "../../../shared/axios.js";
export default {
  components: {
    DashboardTableForm
  },
  emits: ["update"],
  props: {
    update: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  setup(props, {
    emit
  }) {
    const store = useStore();
    const tableOptions = computed(() => store.getters.optionsAndFilters);
    const technicians = ref([]);
    const commonPerformedWorks = ref([]);
    const tableData = ref([]);

    const getStatusType = status => {
      switch (status) {
        case "6:completed":
          return "success";

        case "1:expired":
          return "danger";

        case "2:hour":
          return "warning";

        case "3:day":
          return "";

        case "4:week":
        case "5:none":
          return "info";

        default:
          return "info";
      }
    };

    const getStatus = status => {
      switch (status) {
        case "6:completed":
          return "Выполнено";

        case "1:expired":
          return "Просрочено";

        case "2:hour":
          return "< Часа";

        case "3:day":
          return "< Дня";

        case "4:week":
          return "< недели";

        case "5:none":
          return "> недели";

        default:
          return "Неизвестно";
      }
    };

    const padStr = i => {
      return i < 10 ? `0${i}` : `${i}`;
    };

    const dateToStr = date => {
      // prettier-ignore
      return `${padStr(date.getFullYear())}-${padStr(date.getMonth() + 1)}-${padStr(date.getDate())} ${padStr(date.getHours())}:${padStr(date.getMinutes())}`;
    };

    const mapTableData = () => {
      for (let i = 0; i < tableData.value.length; i++) {
        tableData.value[i].created_at = dateToStr(new Date(tableData.value[i].created_at));

        if (tableData.value[i].done_at) {
          tableData.value[i].done_at = dateToStr(new Date(tableData.value[i].done_at));
        }
      }
    };

    const loading = ref(false);

    const getTableData = (isLoading = true) => {
      loading.value = isLoading;
      axios.post("/dashboard/table", tableOptions.value).then(response => {
        tableData.value = response.data;
        mapTableData();
        loading.value = false;
      }).catch(() => {
        ElMessage.error("Упс! Не удалось загрузить данные!");
      });
    };

    watch(tableOptions, () => {
      getTableData();
    });

    const getTechnicians = () => {
      axios.get("/dashboard/control/technicians").then(response => {
        technicians.value = response.data;
      }).catch(() => {
        ElMessage.error("Упс! Не удалось загрузить данные!");
      });
    };

    const getCommonPerformedWorks = () => {
      axios.get("/dashboard/control/works").then(response => {
        commonPerformedWorks.value = response.data;
      }).catch(() => {
        ElMessage.error("Упс! Не удалось загрузить данные!");
      });
    };

    const submitSuccess = () => {
      ElMessage.success("Успешно сохранено");
      getTableData();
    };

    const submitError = () => {
      ElMessage.error("Упс! Не удалось сохранить данные");
    };

    const changeSorting = ({
      prop,
      order
    }) => {
      store.dispatch("setOptions", {
        orderBy: prop,
        orderDirection: order
      });
    };

    watch(() => props.update, () => getTableData());
    const socket = io(process.env.NODE_ENV === "production" ? "https://mtec-support.herokuapp.com/" : "http://localhost:3000", {
      transports: ["websocket"]
    });
    socket.on("row:new", () => {
      getTableData(false);
      emit("update");
    });
    onMounted(() => {
      getTableData();
      getTechnicians();
      getCommonPerformedWorks();
    });
    return {
      tableData,
      loading,
      technicians,
      commonPerformedWorks,
      getStatusType,
      getStatus,
      submitSuccess,
      submitError,
      changeSorting
    };
  }

};